import React, { useState, useEffect } from 'react'
import ComPagination from '@/components/comPagination/ComPagination.jsx'
import { Input, Table, Modal, Form, Tag } from 'antd';
import moment from 'moment';
import { debounce } from '../../utils/util';
import './Transaction.less'

import { transaction, orderDetails } from "../../api/finance"
import { getCommon } from "../../api/common"

export default function Transaction() {
  // 分页相关
  const [total, setTotal] = useState(200)
  const [params, setParams] = useState({
    keywords: '',
    page: 1,
    limit: 20,
    orderby: 'id',
    sort: 'desc'
  })
  useEffect(() => {
    getTransaction()
  }, [params]);

  // 表格相关

  const [isTableLoading, setIsTableLoading] = useState(false)
  const [datasource, setDatasource] = useState([])
  const changePage = (page, limit) => {
    setParams(params => {
      return { ...params, page, limit }
    })
  }

  const columns = [
    {
      title: 'ID',
      width: 80,
      align: "left",
      dataIndex: 'id',
    },
    {
      title: '订单ID',
      dataIndex: 'order_id',
      key: 'id',
      width: 280
    },
    {
      title: '金额',
      dataIndex: 'amount',
      key: 'amount',
      width: 100
    },
    {
      title: '时间',
      dataIndex: 'create_time',
      key: 'create_time',
      width: 120,
      render: (create_time) => (
        <>
          {moment(create_time * 1000).format("YYYY-MM-DD HH:mm")}
        </>
      )
    },
    {
      title: '支付方式',
      dataIndex: 'gateway',
      key: 'gateway',
      width: 150
    },
    {
      title: '交易流水号',
      dataIndex: 'transaction_number',
      key: 'transaction_number',
      ellipsis:true,
      width: 100,
    }
  ]

  // modal 
  const [hostType] = useState({
    Unpaid: "未付款",
    Pending: "开通中",
    Active: "使用中",
    Suspended: "已暂停",
    Deleted: "已删除",
    Failed: "开通失败"
  }
  )
  const [isModalTableLoding, setIsModalTableLoding] = useState(false)
  const [isModalVisible, setIsModalVisible] = useState(false)
  const handleCancel = () => {
    setIsModalVisible(false);
  };
  // modal 中的表格
  const modalColums = [
    {
      title: '商品名称',
      dataIndex: 'product_name',
      key: 'product_name',
      ellipsis: true,
      width: 200,
      render:(_,{product_name})=>(
        product_name?product_name:"--"
      )
    },
    {
      title: '时间',
      dataIndex: 'create_time',
      key: 'create_time',
      width: 300,
      render: (_, { create_time }) => (
        <>
          {
            <span>{create_time ? moment(create_time * 1000).format("YYYY-MM-DD HH:mm") : "--"}</span>
          }
        </>
      )
    },
    {
      title: '标识',
      dataIndex: 'host_name',
      key: 'host_name',
      width: 200,
      render: (_, { host_name }) => (
        <>
          {
            <span>{host_name ? host_name : "--"}</span>
          }
        </>
      )
    },
    {
      title: '金额/周期',
      dataIndex: 'amount',
      key: 'amount',
      width: 100
    },
    {
      title: '状态',
      dataIndex: 'host_status',
      key: 'host_status',
      width: 100,
      // render: (host_status) => (
      //   <span className='status'>
      //     {host_status}
      //   </span>
      // ),
      render: (_, { host_status, status }) => (
        <>
          {
            status ?
              <Tag color={status === 'Paid' ? 'cyan' : 'red'}>
                {status === 'Paid' ? '已付款' : '未付款'}
              </Tag>
              :
              <span className='status'>{host_status ? host_status : "--"}</span>
          }
        </>
      )
    },
  ]
  const [modalData, setModalData] = useState()

  // 查询交易记录
  const searchOrder = (event) => {
    let keywords = event.target.value
    let page = 1
    setParams(params => {
      return { ...params, page, keywords }
    })
  }
  // 获取订单详情
  const getOrderDetails = (id) => {
    setIsModalTableLoding(true)
    getCommon().then(res => {
      if (res.data.status === 200) {
        const prefix = res.data.data.currency_prefix

        orderDetails(id).then(res => {
          if (res.data.status === 200) {
            const data = res.data.data.order
            let order = []
            order.push(data)
            order.map(item => {
              if (item.amount) {
                item.amount = prefix + item.amount
              }
              if (item.items) {
                let name = "";
                item.items.map(m => {
                  if (m.amount) {
                    m.amount = prefix + m.amount
                  }
                  if (m.billing_cycle) {
                    m.amount += "/" + m.billing_cycle
                  }
                  if (m.host_status) {
                    m.host_status = hostType[m.host_status]
                  }
                  name += m.product_name + "、"
                })
                item.product_name = name.slice(0, -1)
              }
            })
            setModalData(order)
          }
          setIsModalTableLoding(false)
        })
      }
    })

  }
  // 获取交易记录
  const getTransaction = () => {
    setIsTableLoading(true)
    getCommon().then(res => {
      if (res.data.status === 200) {
        const prefix = res.data.data.currency_prefix
        transaction({ ...params }).then(res => {
          if (res.data.status === 200) {
            let count = res.data.data.count;
            setTotal(() => {
              return count
            })
            let list = res.data.data.list
            console.log(list);
            list.map(item => {
              if (item.amount) {
                item.amount = prefix + item.amount
              }
            })
            setDatasource(list)
          }
          setIsTableLoading(false)
        })
      }
    })

  }

  return (
    <div className='com-card transaction'>
      <div className="card-top">
        <p>交易记录</p>
        <Input prefix={<img src={require('../../assets/images/search.png')} />}
          onChange={debounce(searchOrder, 500)}
          className='top-search-input'
          placeholder="请输入" />
      </div>
      <div className="card-main">
        <div className="main_table">
          <Table columns={columns}
            dataSource={datasource}
            pagination={false}
            loading={isTableLoading}
            rowKey={record => record.id}
            scroll={datasource.length !== 0?{
              x: '100%',
              scrollToFirstRowOnChange: true
            }:null}
            onRow={
              (record) => {
                return {
                  onClick: event => {
                    // 点击行 获取当前行的信息
                    const id = record.order_id
                    //  const id = 1145
                    getOrderDetails(id)
                    setIsModalVisible(true)
                  }
                }
              }
            }
          />
        </div>
      </div>
      {/* 分页模块 */}
      <ComPagination total={total} page={params} changePage={changePage}></ComPagination>
      <Modal
        visible={isModalVisible}
        footer={null}
        centered={true}
        closable={false}
        style={{ borderRadius: "10px", overflow: "auto" }}
        width={"60%"}
        maskClosable={true}
        onCancel={handleCancel}
        keyboard={true}
        getContainer={false}
      >
        <div className="modal-title">
          订单详情
        </div>
        <div className="modal-form">
          <Table columns={modalColums}
            dataSource={modalData}
            pagination={false}
            rowKey={record => record.id}
            childrenColumnName="items"
            loading={isModalTableLoding}
            scroll={{
              x: '100%',
              scrollToFirstRowOnChange: true
            }}
          />
        </div>
      </Modal>

    </div>
  )
}
